import React from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import './styles/app.scss';
import MainPage from './pages/main_page/';
import CatalogAllTypes from './pages/catalog/';
import CatalogTypes from './pages/catalog_types/';
import CatalogTypeMarks from './pages/catalog_types_marks/';
import Header from './components/header';
import Footer from './components/footer';

export default () => {
  return (
    <Router>
      <Header />
      <Switch>
        <Route path="/" exact component={MainPage} />
        <Route path="/catalog" exact component={CatalogAllTypes} />
        <Route path="/catalog/:type" exact component={CatalogTypes} />
        <Route path="/catalog/:type/:mark" exact component={CatalogTypeMarks} />
      </Switch>
      <Footer />
    </Router>
  );
};
